import React, { useState } from 'react';
import { TextField, MenuItem, Autocomplete, Container, Typography, Button, CircularProgress, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Slider } from '@mui/material';
import { useNavigate } from 'react-router-dom';

const roles = [
  'Team leader',
  'Product manager',
  'Scrum master',
  'Engineering Manager',
  'Project Manager',
  'Product owner',
  'Desenvolvedor(a)',
  'Technical leader',
  'Agile Master',
  'IT Director',
  'Agile Coach',
  'Gerente de Servios',
  'Customer success',
  'Superintendente',
  'Pesquisador',
  'Head comercial',
  'Governana',
  'Digital Analytics',
  'DevOps/SRE',
  'Consultor de implantao', 
  'Arquiteto de software', 
  'Administrador de Dados, DBA',
  'CTO',
  'CIO',
  'CEO',
];
const experienceLevels = ['0 a 5', '6 a 9', '10 a 20', 'Mais de 20'];
const companySizes = ['Microempresa (< 10 empregados)', 'Pequena empresa (10-99 empregados)', 'Mdia empresa (100-500 empregados)', 'Grande empresa (>500 empregados)'];
const agileMethods = ['Scrum', 'Kanban', 'Lean', 'Safe', 'XP', 'ScrumBan'];
const ritualsToUse = ['Reunio de Planejamento', 'Sprint Review', 'Reunio Semanal', 'Reunio Diria (daily)', 'Retrospectiva'];



const ContentRecommendation = () => {
  const [role, setRole] = useState('');
  const [experience, setExperience] = useState('');
  const [companySize, setCompanySize] = useState('');
  const [methods, setMethods] = useState([]);
  const [rituals, setRituals] = useState([]);
  const [loading, setLoading] = useState(false);
  const [recommendations, setRecommendations] = useState([]);
  const [error, setError] = useState('');  
  const [threshold, setThreshold] = useState(0.5); // Novo estado para o threshold

  const handleThresholdChange = (event, newValue) => {
    setThreshold(newValue);
  };

  const handleRecommendation = async () => {
    setLoading(true);
    setError('');
    setRecommendations([]);
  
    try {
      const currentUrl = window.location.origin;
      const response = await fetch( currentUrl + '/recommend_metrics_content', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          role: role, 
          years_exp: experience, 
          org_size: companySize.replace(/\s*\(.*\)/, ''),
          use_metrics_planning: rituals.includes('Reunio de Planejamento') ? 1 :  0,
          use_metrics_review: rituals.includes('Sprint Review') ? 1 :  0,
          use_metrics_weekly: rituals.includes('Reunio Semanal') ? 1 :  0,
          use_metrics_daily: rituals.includes('Reunio Diria (daily)') ? 1 :  0,
          use_metrics_retro: rituals.includes('Retrospectiva') ? 1 :  0,
          agile_methods_scrum: methods.includes('Scrum') ? 1 :  0,
          agile_methods_kanban: methods.includes('Kanban') ? 1 :  0,
          agile_methods_scrumban: methods.includes('ScrumBan') ? 1 :  0,
          agile_methods_xp: methods.includes('XP') ? 1 :  0,
          agile_methods_safe: methods.includes('Safe') ? 1 :  0,
          agile_methods_lean: methods.includes('Lean') ? 1 :  0,
          threshold: parseFloat(threshold),
        }),
      });

      if (!response.ok) {
        throw new Error('Erro ao obter as recomendaes da API.');
      }

      const data = await response.json();

      if (data.metric_recommendations && data.metric_recommendations['0']) {
        setRecommendations(data.metric_recommendations['0']);
      } else {
        setError('Nenhuma mtrica recomendada.');
      }
    } catch (err) {
      setError('Erro ao buscar as recomendaes. Verifique a API e tente novamente.');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const navigate = useNavigate();
  return (
    <Container maxWidth="md" style={{ marginTop: '50px', padding: '20px', backgroundColor: '#fff', borderRadius: '10px' }}>
      <Typography variant="h4" align="center" gutterBottom>
        Recomendao de Mtricas utilizando um modelo de classificao e abordagem baseada em contedo
      </Typography>

      {/* Seleo da Funo */}
      <TextField
        select
        label="Selecione sua funo"
        value={role}
        onChange={(e) => setRole(e.target.value)}
        fullWidth
        margin="normal"
      >
        {roles.map((option) => (
          <MenuItem key={option} value={option}>
            {option}
          </MenuItem>
        ))}
      </TextField>

      {/* Seleo dos Anos de Experincia */}
      <TextField
        select
        label="Anos de experincia"
        value={experience}
        onChange={(e) => setExperience(e.target.value)}
        fullWidth
        margin="normal"
      >
        {experienceLevels.map((option) => (
          <MenuItem key={option} value={option}>
            {option}
          </MenuItem>
        ))}
      </TextField>

      {/* Seleo do Tamanho da Empresa */}
      <TextField
        select
        label="Tamanho da empresa"
        value={companySize}
        onChange={(e) => setCompanySize(e.target.value)}
        fullWidth
        margin="normal"
      >
        {companySizes.map((option) => (
          <MenuItem key={option} value={option}>
            {option}
          </MenuItem>
        ))}
      </TextField>

    
      <Autocomplete
        multiple
        id="agile-methods"
        options={agileMethods}
        value={methods}
        onChange={(event, newValue) => {
          setMethods(newValue);
        }}
        renderInput={(params) => (
          <TextField {...params} label="Mtodos geis utilizados" placeholder="Selecione" fullWidth margin="normal" />
        )}
      />

      <Autocomplete
        multiple
        id="rituals"
        options={ritualsToUse}
        value={rituals}
        onChange={(event, newValue) => {
          setRituals(newValue);
        }}
        renderInput={(params) => (
          <TextField {...params} label="Rituais geis utilizados" placeholder="Selecione" fullWidth margin="normal" />
        )}
      />

      
      {/* Slider para Threshold de Similaridade */}
      <Typography variant="body1" style={{ marginTop: '20px' }}>
        Threshold de Similaridade (%): {threshold} </Typography>
      <Slider
        value={threshold}
        onChange={handleThresholdChange}
        step={0.1}
        min={0}
        max={1}
        valueLabelDisplay="auto"
        marks={[{ value: 0, label: '0' }, { value: 1, label: '1' }]}
        style={{ marginBottom: '20px' }}
      />

      {/* Boto para Obter Recomendaes */}
      <Button
        variant="contained"
        color="primary"
        onClick={handleRecommendation}
        fullWidth
        style={{ marginTop: '20px', padding: '10px' }}
        disabled={loading}
      >
        {loading ? <CircularProgress size={24} color="inherit" /> : 'Obter Recomendaes'}
      </Button>

      {/* Exibir Erro, se houver */}
      {error && (
        <Typography color="error" variant="body2" style={{ marginTop: '10px' }}>
          {error}
        </Typography>
      )}

  {recommendations.length > 0 && (
        <TableContainer component={Paper} style={{ marginTop: '20px' }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell><strong>Mtrica</strong></TableCell>
                <TableCell><strong>Afinidade</strong></TableCell>
                <TableCell><strong>Categoria</strong></TableCell>
                <TableCell><strong>Descrio</strong></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {recommendations.map((rec, index) => (
                <TableRow key={index}>
                  <TableCell>{rec.metric}</TableCell>
                  <TableCell>{(rec.affinity * 100).toFixed(2)} %</TableCell>
                  <TableCell>{rec.category}</TableCell>
                  <TableCell>{rec.description}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      <Button 
        variant="outlined" 
        color="primary" 
        fullWidth 
        style={{ marginTop: '20px' }}
        onClick={() => navigate('/')}
      >
        Voltar para a Pgina Inicial
      </Button>
      
       {/* Rodap com imagem e texto */}
       <footer style={{ marginTop: '50px', textAlign: 'center' }}>
        <img 
          src="logo_ufsc.png" 
          alt="Imagem do Trabalho" 
          style={{ width: '100%', maxWidth: '50px', marginBottom: '10px' }} 
        />
        <Typography variant="caption" display="block" gutterBottom>
          Trabalho de Concluso de Curso apresentado ao Curso de Graduao em Sistemas de Informao 
          pela Universidade Federal de Santa Catarina. Aluno Vinicius Araldi Lunardi Farias, 
          matrcula 18200653. Orientador Prof. Dr. Jean Carlo Rossa Hauck.
        </Typography>
      </footer>
    </Container>
  );
};

export default ContentRecommendation;
